import React from "react";
import '../css/calculator.css'


function Calculator() {
    const [result, setresult] = React.useState('')
    const display = (Num) => {
        setresult(result + Num)
    }
    const disym = (Sym) => {
        setresult(result+Sym)
    }

    const clearfn = () => {
        let splitarr= result.split('');
        if(splitarr.length>0){
            splitarr.pop();
            setresult(splitarr.join(''))
        }
    }

    // const allclear = ()=>{ 
    //     setresult('')
    // }
    // const calculate = ()=>{
    //     setresult(eval(result))
    // }
    return (
        <div id="calc">

            <input type="text" value={result} disabled placeholder="0" /><br />
            <button type="button" onClick={() => display('1')}>1</button>
            <button type="button" onClick={() => display('2')}>2</button>
            <button type="button" onClick={() => display('3')}>3</button>
            <button type="button" onClick={() => display('4')}>4</button><br />
            <button type="button" onClick={() => display('5')}>5</button>
            <button type="button" onClick={() => display('6')}>6</button>
            <button type="button" onClick={() => display('7')}>7</button>
            <button type="button" onClick={() => display('8')}>8</button><br />
            <button type="button" onClick={() => display('9')}>9</button>
            <button type="button" onClick={() => display('0')}>0</button>
            <button type="button" id="symbol" onClick={() => disym('+')}>+</button>
            <button type="button" id="symbol" onClick={() => disym('-')}>-</button><br />
            <button type="button" id="symbol" onClick={() => disym('*')}>*</button>
            <button type="button" id="symbol" onClick={() => disym('/')}>/</button>
            <button type="button" id="symbol" onClick={() => disym('.')}>.</button>
            <button type="button" className="clear" onClick={() => clearfn()}>C</button><br />
            <button type="button" className="clear" id="lastbuttons" onClick={() => setresult('')}>AC</button>
            <form >
                <button type="button" id="lastbuttons" onClick={()=> setresult(eval(result))}>=</button>
            </form>


        </div>)
}

export default Calculator;