import React from "react";
import '../css/card.css';
import profile from "../assets/emp-removebg-preview.png";

function Card(props) {
    return(
        <div className="card">
            <img src={profile}
                alt=""/>
            <p>{props.emp.Name}</p>
            <p>{props.emp.EmployeeID}</p>
            <button onClick={props.click}>{props.btntext}</button>
        </div>
    )
}

export default Card;

