import React from "react";
import "../css/signup.css"

function Signup() {
    const onsign = (Name) =>{
        alert('Bye '+Name)
    }

    return (
        <div id="signupform">
            <label htmlFor="username"><i className="fa-solid fa-user fa-xs"></i> Username</label>
            <input   type="text" id="username" placeholder="Type your name" />
            <br />
            <label htmlFor="password"><i className="fa-solid fa-lock fa-xs"></i> Password</label>
            <input type="password" id="password" placeholder="Type your password" />
            <br />
            <label htmlFor="repassword"><i className="fa-solid fa-lock fa-xs"></i> Retype Password</label>
            <input type="password" id="repassword" placeholder="Retype password" />


            <div style={{textAlign: 'center'}}>
            <button type="submit" onClick={()=>onsign('Madhu')}><i className="fa-solid fa-right-from-bracket "></i> SIGN-UP</button>
            <br />
            </div>
        </div>
    )
}
export default Signup;