import React from "react";
import Table from "../components/table";

function Pagination() {
    // const data = [];
    const [data, setdata] = React.useState([]);

    const getdata = () => {
        fetch('https://jsonplaceholder.typicode.com/posts')
            .then(response => response.json())
            .then(responsedata => {
                // data=responsedata;
                setdata(responsedata)
                console.log(responsedata);
            }) 
    }
    React.useEffect(() => {{/*if u use useState in this case it will keep giving request to call the data so use useEffect here */}
        getdata();
    }, [])
    // getdata();
    return (
        <div>
            {data.length > 0 ?
                <Table data={data}></Table> : 'LOADING...'}

        </div>
    )
}

export default Pagination;